nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #1b2430;
  padding: 20px 80px;
  /* margin: 10px; */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
}
#navbar {
  align-items: center;
  display: flex;

  justify-content: center;
}
#navbar li {
  padding: 0 20px;
  list-style: none;
}
#navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: white;
  transition: 0.3s ease-in-out;
}
#navbar li a:hover,
#navbar li a.active {
  color: #17cf97;
}
#navbar li a:hover::after,
#navbar li a.active::after {
  content: "";
  width: 5%;
  height: 2px;
  background: #17cf97;
  position: absolute;
  bottom: -4px;
  left: 20px;
}
#mobile {
  display: none;
}
#mobile i {
  color: white;
  align-items: center;
}
@media screen and (max-width: 769px) {
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: 0px;
    width: 300px;
    height: 100vh;
    background-color: #2a3239;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0) 0.1;
    padding: 40px 0 0 10px;
  }
  #mobile {
    display: block;
  }
  #navbar li {
    margin-bottom: 25px;
  }
  #mobile i {
    font-size: 24px;
    cursor: pointer;
  }
}
